.copilot-panel {
  width: 350px;
  height: 100%;
  background-color: var(--bg-secondary);
  border-left: 1px solid var(--border-primary);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  flex-shrink: 0;
}
.copilot-header {
  padding: 8px 12px;
  border-bottom: 1px solid var(--border-primary);
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: var(--bg-tertiary);
  flex-shrink: 0;
}
.copilot-icon {
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--accent-purple);
}
.copilot-title {
  flex: 1;
}
.copilot-name {
  font-size: 11px;
  font-weight: 600;
  color: var(--text-primary);
}
.copilot-subtitle {
  font-size: 10px;
  color: var(--text-secondary);
}
.copilot-messages {
  flex: 1;
  overflow-y: auto;
  padding: 8px 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.copilot-message {
  display: flex;
  gap: 8px;
  animation: slideIn 0.3s ease-out;
}
.message-avatar {
  width: 18px;
  height: 18px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
}
.copilot-message.bot .message-avatar {
  color: var(--accent-purple);
}
.message-content-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.message-sender-name {
  font-size: 10px;
  font-weight: 600;
  color: var(--text-primary);
}
.message-text {
  font-size: 11px;
  line-height: 1.4;
  color: var(--text-primary);
  word-wrap: break-word;
}
.copilot-suggestions {
  padding: 0 12px 6px;
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  flex-shrink: 0;
}
.copilot-suggestion {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 4px 8px;
  background-color: var(--bg-tertiary);
  border: 1px solid var(--border-primary);
  border-radius: 3px;
  color: var(--text-primary);
  font-size: 10px;
  cursor: pointer;
  transition: background-color 0.2s, border-color 0.2s;
}
.copilot-suggestion:hover {
  background-color: rgba(88, 166, 255, 0.1);
  border-color: var(--accent-blue);
}
.copilot-suggestion svg {
  color: var(--accent-blue);
  flex-shrink: 0;
}
.copilot-input-container {
  padding: 8px 12px;
  border-top: 1px solid var(--border-primary);
  background-color: var(--bg-tertiary);
  display: flex;
  gap: 6px;
  flex-shrink: 0;
}
.copilot-input {
  flex: 1;
  background-color: var(--bg-secondary);
  border: 1px solid var(--border-primary);
  border-radius: 3px;
  padding: 4px 8px;
  color: var(--text-primary);
  font-size: 11px;
  font-family: inherit;
  outline: none;
  transition: border-color 0.2s;
}
.copilot-input:focus {
  border-color: var(--accent-blue);
}
.copilot-input::placeholder {
  color: var(--text-tertiary);
}
.copilot-send {
  width: 24px;
  height: 24px;
  border-radius: 3px;
  background-color: transparent;
  border: 1px solid var(--border-primary);
  color: var(--text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s, border-color 0.2s;
  flex-shrink: 0;
}
.copilot-send svg {
  width: 12px;
  height: 12px;
}
.copilot-send:hover:not(:disabled) {
  background-color: var(--accent-blue);
  border-color: var(--accent-blue);
  color: white;
}
.copilot-send:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.terminal-panel {
  width: 100%;
  height: 250px;
  background-color: var(--bg-primary);
  border-top: 1px solid var(--border-primary);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  flex-shrink: 0;
  font-family: 'Fira Code', 'Consolas', monospace;
}
.terminal-header {
  padding: 8px 12px;
  background-color: var(--bg-secondary);
  border-bottom: 1px solid var(--border-primary);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
}
.terminal-title {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 11px;
  font-weight: 600;
  color: var(--text-primary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.terminal-title svg {
  color: var(--text-secondary);
}
.terminal-close {
  background: transparent;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  padding: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  transition: background-color 0.2s, color 0.2s;
}
.terminal-close:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: var(--text-primary);
}
.terminal-content {
  flex: 1;
  overflow-y: auto;
  padding: 12px;
  color: var(--text-primary);
  font-size: 13px;
  line-height: 1.5;
}
.terminal-line {
  margin-bottom: 4px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.terminal-input-line {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 4px;
}
.terminal-prompt {
  color: var(--accent-green);
  font-weight: 600;
}
.terminal-input {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  color: var(--text-primary);
  font-family: inherit;
  font-size: inherit;
  caret-color: var(--accent-green);
}
.terminal-input::selection {
  background-color: rgba(88, 166, 255, 0.3);
}
.main-content-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
@media (max-width: 480px) {
  body {
    font-size: 12px;
  }
  .menu-bar {
    height: 40px;
    padding: 0 4px;
  }
  .menu-item {
    padding: 4px 8px;
    font-size: 12px;
  }
  .search-bar {
    display: none;
  }
  .activity-bar {
    width: 40px;
  }
  .activity-icon {
    width: 40px;
    height: 40px;
  }
  .activity-icon svg {
    width: 20px;
    height: 20px;
  }
  .sidebar {
    width: 100%;
    position: absolute;
    z-index: 100;
    height: calc(100vh - 40px);
  }
  .sidebar-header {
    font-size: 11px;
    padding: 8px;
  }
  .editor-container {
    width: 100%;
  }
  .tabs-container {
    overflow-x: auto;
    scrollbar-width: thin;
  }
  .tab {
    min-width: 100px;
    font-size: 11px;
  }
  .copilot-panel {
    width: 100% !important;
    height: 250px !important;
    border-left: none !important;
    border-top: 1px solid var(--border-primary) !important;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 200;
  }
  .terminal-panel {
    height: 180px;
  }
  .markdown-content h1 {
    font-size: 24px !important;
  }
  .markdown-content h2 {
    font-size: 18px !important;
  }
  .markdown-content h3 {
    font-size: 16px !important;
  }
  .markdown-content p {
    font-size: 12px !important;
  }
}
@media (min-width: 481px) and (max-width: 768px) {
  body {
    font-size: 13px;
  }
  .menu-bar {
    height: 38px;
  }
  .search-bar {
    max-width: 200px;
  }
  .activity-bar {
    width: 45px;
  }
  .activity-icon {
    width: 45px;
    height: 45px;
  }
  .sidebar {
    width: 220px;
  }
  .copilot-panel {
    width: 100% !important;
    height: 280px !important;
    border-left: none !important;
    border-top: 1px solid var(--border-primary) !important;
  }
  .terminal-panel {
    height: 200px;
  }
  .vscode-container {
    flex-direction: column;
  }
}
@media (min-width: 769px) and (max-width: 1024px) {
  .sidebar {
    width: 240px;
  }
  .copilot-panel {
    width: 300px !important;
  }
  .search-bar {
    max-width: 300px;
  }
}
@media (min-width: 1025px) {
  .sidebar {
    width: 280px;
  }
  .copilot-panel {
    width: 350px !important;
  }
  .search-bar {
    max-width: 400px;
  }
}
@media (max-height: 500px) and (orientation: landscape) {
  .menu-bar {
    height: 32px;
  }
  .activity-bar {
    width: 40px;
  }
  .activity-icon {
    width: 40px;
    height: 40px;
  }
  .sidebar {
    width: 200px;
  }
  .terminal-panel {
    height: 150px;
  }
  .copilot-panel {
    width: 250px !important;
  }
}
@media (hover: none) and (pointer: coarse) {
  .menu-item,
  .activity-icon,
  .tab,
  .file-item {
    min-height: 44px;
    display: flex;
    align-items: center;
  }
  .tab-close {
    min-width: 32px;
    min-height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  ::-webkit-scrollbar {
    width: 12px;
    height: 12px;
  }
}
@media print {
  .menu-bar,
  .activity-bar,
  .sidebar,
  .copilot-panel,
  .terminal-panel {
    display: none !important;
  }
  .editor-container {
    width: 100% !important;
    border: none !important;
  }
  body {
    background: white;
    color: black;
  }
}
